<template>
    <div class="box">
      <div class="header">
        <i class="iconfont icon-zuojiantou" @click="go"></i>
        <slot name="content"></slot>
        <slot name="can"></slot>
      </div>

    </div>
</template>

<script>
    export default {
        data() {
            return {}
        },
        methods: {
          go(){
            this.$router.go(-1)
          }
        },
        components: {},
        computed: {}

    }

</script>
<style scoped>
.box{
  width: 100%;
  background: #19a9ba;
  height: .45rem;
}

.header{
  width: 100%;
  align-items: center;
  text-align: center;
  display: flex;
  justify-content: space-between;
  height: 0.44rem;
  line-height: 0.44rem;
}
.icon-zuojiantou{
  color:#75ffff;
  font-size: 0.18rem;
  margin-left: .1rem;
}
  .slot{

  }
  .p{
    font-size: .18rem;
    color: #fff;
  }
</style>



