<template>
    <div class="box-wrap">
      <Header>
        <template class="hidden" v-slot:content><span class="enroll">注册</span></template>
        <template class="hidden" v-slot:can><div class="hidden"></div></template>
        </Header>
      <div class="account"  >
        <div class="number">
          <div class="number-wrap">
            <label for="cel" class="label-cell">账号
              <input type="text" id="cel" class="cei" placeholder="手机号/邮箱/用户名">
            </label>
          </div>
          <label for="cod" class="label-cell label1">密码
            <input type="text" id="cod" class="cod" placeholder="请输入密码">
          </label>
          <label for="cods" class="label-cell label1">验证码
            <input type="text" id="cods" class="cod codes" placeholder="请输入验证码">
            <button class="btn">获取验证码</button>
          </label>
        </div>
        <div class="login-wrap">
          <button class="login">注册</button>

        </div>
      </div>
      <div class="footer">
        <div class="footer-box">
          <p>毕业设计作业</p>
        </div>
      </div>
    </div>
</template>

<script>
  import Header from '../../header'
    export default {
        data() {
            return {}
        },
        methods: {},
        components: {
          Header
        },
        computed: {}

    }

</script>
<style scoped>
.box-wrap{
  background: #f3f9fc;
}
.codes{
 margin-left: 0;
}
.hidden{
  width: .2rem;
  height: .3rem;
}
.enroll{
    font-size: .18rem;
  color: #fff;
  }
.number{
  margin-top:0.1rem ;
  border-top:0.01rem solid rgb(221,221,221)
}
.headButton p{
  font-size: 0.14rem;
  text-align: center;
  line-height: .32rem;
}

.number-wrap{
  padding-left: 0.05rem;
}

.label-cell{
  border-bottom: 1px solid #ccc;
  display: block;
  line-height: .45rem;
  height: .45rem;
  color: #19a9ba;
}
.label1{
  /*margin-left: 0.05rem;*/
  text-indent: 0.06rem;
}
.cei{
  text-indent: 0.05rem;
  border:none;
  outline: none;
  width: 1.5rem;
  margin-left: .25rem;
  height: .4rem;
  background: #f3f9fc;
}
.cod{
  text-indent: 0.05rem;
  border:none;
  outline: none;
  width: 1.2rem;
  margin-left: .25rem;
  height: .4rem;
  background: #f3f9fc;
}
.btn{
  width: 0.9rem;
  height: 0.25rem;
  border-radius: 5px;
  background: #fff;
  color: #d7dce0;
  border:0.01rem solid  #ccc;
}
.login-wrap{
  text-align: center;
  margin-top: 0.15rem;
  padding-bottom: 0.05rem;
}
.login{
  background: #85d1db;
  border-radius: 5px ;
  width: 3rem;
  height: .5rem;
  text-align: center;
  font-size: .18rem;
  color: #fff;
}

.footer-box>p{
  font-size: .12rem;
  color: #d7dce0;
}
.cei::-webkit-input-placeholder{
  font-size: .16rem;
}
.cod::-webkit-input-placeholder{
  font-size: .16rem;
}
.footer{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: #f3f9fc;

}
.footer-box{
  border-top:0.01rem solid ;
  text-align: center;
  padding-top: .1rem;
  padding-bottom: 0.2rem;
}
.footer-box>p{
  font-size: .12rem;
  color: #d7dce0;
}

</style>



