<template>
    <div class="box">
      <Header>
        <template   v-slot:content><span class="login-head">登录</span></template>
        <template v-slot:can><span class="head" @click="goto">注册</span></template>
      </Header>
      <div class="headButton">
        <div class="note":class="{cut:cuts}" @click="cut">
          <p>短信验证码登录</p>
        </div>
        <div class="iphone" :class="{cut:!cuts}" @click="pos">
          <p>账号登录</p>
        </div>
      </div>

        <div class="box-wrap">
          <div class="note-login" v-show="hidden">
            <div class="number">
              <div class="number-wrap">
                <label for="cell" class="label-cell">手机号
                  <input type="text" id="cell" class="cei" placeholder="请输入手机号">
                </label>
              </div>
              <label for="code" class="label-cell label1">验证码
                <input type="text" id="code" class="cod" placeholder="请输入验证码">
                <button class="btn">获取验证码</button>
              </label>
            </div>
            <div class="login-wrap">
              <button class="login">登录</button>
            </div>
          </div>
          <div class="account" v-show="!hidden" >
            <div class="number">
              <div class="number-wrap">
                <label for="cel" class="label-cell">账号
                  <input type="text" id="cel" class="cei" placeholder="手机号/邮箱/用户名">
                </label>
              </div>
              <label for="cod" class="label-cell label1">密码
                <input type="text" id="cod" class="cod" placeholder="请输入密码">
              </label>
            </div>
            <div class="login-wrap">
              <button class="login">登录</button>
              <p class="find">找回密码</p>
            </div>
          </div>
        </div>


      <div class="footer">
          <div class="footer-box">
            <p>毕业设计作业</p>
          </div>
      </div>
    </div>
</template>

<script>
  import Header from '../../header'
    export default {
        data() {
            return {
              hidden:true,
              cuts:true
            }
        },
        methods: {
          cut(){
            this.hidden=true,
              this.cuts=true
          },
          pos(){
            this.hidden=false
            this.cuts=false
          },
          goto(){
            this.$router.push('./register')
          }

        },
        components: {
          Header
        },
        computed: {}

    }

</script>
<style scoped>
  .box{
    width: 100%;
    height: 100%;

  }
  .login-head{
    font-size: .18rem;
    margin-left: .2rem;
    color: #ffff;
  }
  .box-wrap{
    background: #f3f9fc;
  }
  .number{
    margin-top:0.1rem ;
    border-top:0.01rem solid rgb(221,221,221)
  }


  .note{
    width: 1.8rem;
    line-height: .32rem;
    height: .32rem;
    border-radius: 0.05rem 0 0 0.05rem;
    color: #fff;
    border:1px solid #fff;
    box-sizing: border-box;
  }
  .iphone{
    width: 1.8rem;
    line-height: .32rem;
    height: .32rem;
    border-radius:0 0.05rem   0.05rem 0;
    color: #fff;
    background: #18a9b9;
    border:1px solid #fff;
    box-sizing: border-box;
  }
  .head{
    font-size: 0.16rem;
    color: #75ffff;
    margin-right: .1rem;
  }
  .headButton p{
    font-size: 0.14rem;
    text-align: center;
    line-height: .32rem;
  }
  .headButton{
    box-sizing: border-box;
    line-height: .45rem;
    height: .45rem;
    padding: 0  0.05rem;
    background: #19a9ba;
    display: flex;
  }
  .number-wrap{
    padding-left: 0.05rem;
  }

  .label-cell{
    border-bottom: 1px solid #ccc;
    display: block;
    line-height: .45rem;
    height: .45rem;
    color: #19a9ba;
  }
  .label1{
    /*margin-left: 0.05rem;*/
    text-indent: 0.06rem;
  }
  .cei{
    text-indent: 0.05rem;
    border:none;
    outline: none;
    width: 1.5rem;
    margin-left: .25rem;
    height: .4rem;
    background: #f3f9fc;
  }
  .cod{
    text-indent: 0.05rem;
    border:none;
    outline: none;
    width: 1.2rem;
    margin-left: .25rem;
    height: .4rem;
    background: #f3f9fc;
  }
  .btn{
    width: 0.9rem;
    height: 0.25rem;
    border-radius: 5px;
    background: #fff;
    color: #d7dce0;
    border:0.01rem solid  #ccc;
  }
  .login-wrap{
    text-align: center;
    margin-top: 0.15rem;
    padding-bottom: 0.05rem;
    }
  .login{
    background: #85d1db;
    border-radius: 5px ;
    width: 3rem;
    height: .5rem;
    text-align: center;
    font-size: .18rem;
    color: #fff;
  }
  .footer{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #f3f9fc;

  }
  .footer-box{
    border-top:0.01rem solid ;
    text-align: center;
    padding-top: .1rem;
    padding-bottom: 0.2rem;
  }
  .footer-box>p{
    font-size: .12rem;
    color: #d7dce0;
  }
.cei::-webkit-input-placeholder{
  font-size: .16rem;
}
  .cod::-webkit-input-placeholder{
    font-size: .16rem;
  }
.find{
  text-align: right;
  font-size: .14rem;
  margin-right: 0.1rem;
  color: #00afc7;
  padding-top: 0.05rem
}
  .cut{
    background: #fff;
    color: #25a4bb;
  }
</style>



