import Vue from 'vue'
import Router from 'vue-router'
import  Login from '../components/Login/login-child/login'
import Mobile from '../components/Login/mobile'
import Register from '../components/Login/login-child/register'
import Home from '../components/Home/home'
import HotelIndex from '../components/hotel/hotelIndex'
import Group from '../components/hotel/hotelGroup/group'
import  Search from '../components/hotel/hotelGroup/searchCity'
import  Gx from '../components/Home/iconAll/gonglue'
Vue.use(Router);

export default new Router({
  routes: [
    {
      path:'/',
      component:Home
    },
    {
      path: '/mobile/login',
      component: Login
    },
    {
      path:'/mobile',
      component:Mobile
    },
    {
      path:'/mobile/register',
      component:Register
    },
    {
      path:'/hotelIndex',
      component:HotelIndex
    },
    {
      path:'/group',
      component:Group,
      children:[
        {
          path:'searchCity',
          component:Search
        }
      ]
    },
    {
      path:'/gx',
      component:Gx
    }
  ]
})






