                 <div class="city-hotel line">
                   <p class="city-hotel-p city-span-hotel"><span>搜索酒店名、地名、地标</span></p>
                 </div>
                 <div class="city-content-div">
                   <i class="iconfont icon-iconfonti right"></i>
