        <div class="about-wrap">
          <div class="about">
            <div class="look borderRight">最近浏览</div>
            <div class="dist">我的订单</div>
          </div>
        </div>
      </div>


</template>
<script>
    export default {
        data() {
            return {
              stores:true,
              city:'',
              cit:''
            }
        },
        methods: {
          str(){
            this.stores=true;
            this.cit=this.city
          },
          cities(){
            var that=this
            var map = new BMap.Map("allmap");
            function myFun(result){
              var cityName = result.name;
              map.setCenter(cityName);
               that.city=cityName
            }

            var myCity = new BMap.LocalCity();
            myCity.get(myFun);
          },
          hidden(){
            this.stores=false
          },
          goto(){
            this.$router.go(-1)
          }

        },
      mounted(){

      },
        components: {},
        computed: {

        }

    }

</script>
