    font-size: .18rem;
    color: #fff;
    display: block;
    padding: .1rem;
  }
  .search-wrap{
    background: #fff;
  }
  .bg-div{
    margin-top: 0.05rem;
    text-align: center;
  }
  .bg-span{
    color: #fff;
    font-size: .12rem;
    display: block;
    margin-right: .1rem;
  }
  .icon-fapiao{
    margin-right: .1rem;
    font-size: .18rem;

    color: #fff;
  }
  .search{
    padding: .1rem .1rem 0

  }
