(function(window,document){
  let doE=document.documentElement,
  resizeEvt='orientationchange' in window ? 'orientationchange' : 'resize',
    recale=function () {
      let clientWidth=doE.clientWidth;
      if(!clientWidth) return;
      doE.style.fontSize=100*(clientWidth/320)+'px'
    };
  if(!doE.addEventListener) return;
  window.addEventListener(resizeEvt,recale,false);
  document.addEventListener("DOMContentLoaded",recale,false)

})(window,document)



